body {
  font-family: Arial, sans-serif;
}

.search-form {
  max-width: 600px;
  align-items: center;
  margin: 20px auto;
  display: flex;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  padding: 10px 20px;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 10px;
  padding: 20px;
  display: grid;
}

.photo-card {
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.info {
  padding: 10px;
}

.info-item {
  margin-bottom: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  margin: 20px auto;
  padding: 10px 20px;
}

